.backdrop {
  width: 100%;
  height: 110%;
  z-index: 3;
  backdrop-filter: blur(3px);
  background: rgba(74, 16, 116, .235);
  transition-property: opacity, visibility;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop--hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

